<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click.prevent="minusAmount">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" name="count" v-model.number="productAmount" @input="test"/>

    <button type="button" aria-label="Добавить один товар" @click.prevent="plusAmount">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: ['amount'],
  data() {
    return {
      productAmount: this.amount,
    };
  },
  methods: {
    plusAmount() {
      this.productAmount += 1;
      this.$emit('changeAmount', this.productAmount);
    },
    minusAmount() {
      if (this.productAmount > 1) {
        this.productAmount -= 1;
        this.$emit('changeAmount', this.productAmount);
      }
    },
    test() {
      this.$emit('changeAmount', this.productAmount);
    },
  },
};
</script>
