<template>
  <aside class="filter">
    <h2 class="filter__title">Фильтры</h2>

    <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price" for="min-price">
          <input
            class="form__input"
            type="text"
            name="min-price"
            id="min-price"
            v-model.number="currentPriceFrom"/>
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price" for="max-price">
          <input
            class="form__input"
            type="text"
            name="max-price"
            id="max-price"
            v-model.number="currentPriceTo"/>
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select" for="category">
          <select class="form__select" type="text" name="category" id="category" v-model.number="currentCategoryId">
            <option value="0" selected>Все категории</option>
            <option v-for="category in categories" :value="category.id" :key="category.id">{{ category.title }}</option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors">
          <li class="colors__item" v-for="(concColor, index) in colors" :key="concColor.id">
            <label class="colors__label" :for="`color_${concColor.id}`">
              <input
                class="colors__radio sr-only"
                type="radio"
                name="color"
                :value="`${concColor.id}`"
                :id="`color_${concColor.id}`"
                v-model.number="currentColorId"
                :checked="index === 0"/>
              <span class="colors__value" :style="`background-color: ${concColor.code}`"> </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Объемб в ГБ</legend>
        <ul class="check-list">
          <li class="check-list__item">
            <label class="check-list__label" for="volume_8">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="8"
              checked="" id="volume_8"/>
              <span class="check-list__desc">
                8
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label" for="volume_16">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="16"
              id="volume_16"/>
              <span class="check-list__desc">
                16
                <span>(461)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label" for="volume_32">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="32"
              id="volume_32"/>
              <span class="check-list__desc">
                32
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label" for="volume_64">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="64"
              id="volume_64"/>
              <span class="check-list__desc">
                64
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label" for="volume_128">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="128"
              id="volume_128" />
              <span class="check-list__desc">
                128
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label" for="volume_264">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="264"
              id="volume_264"/>
              <span class="check-list__desc">
                264
                <span>(313)</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit">Применить</button>
      <button class="filter__reset button button--second" type="button" @click.prevent="reset">Сбросить</button>
    </form>
  </aside>
</template>

<script>
import axios from 'axios';
import API_BASE_URL from '@/config';

export default {
  data() {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      currentColorId: 0,
      categoriesData: null,
      colorsData: null,
    };
  },
  props: ['priceFrom', 'priceTo', 'categoryId', 'colorId'],
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    colors() {
      return this.colorsData ? this.colorsData.items : [];
    },
  },
  watch: {
    priceFrom(val) {
      this.currentPriceFrom = val;
    },
    priceTo(val) {
      this.currentPriceTo = val;
    },
    categoryId(val) {
      this.currentCategoryId = val;
    },
    colorId(val) {
      this.currentColorId = val;
    },
  },
  methods: {
    submit() {
      this.$emit('update:priceFrom', this.currentPriceFrom);
      this.$emit('update:priceTo', this.currentPriceTo);
      this.$emit('update:categoryId', this.currentCategoryId);
      this.$emit('update:colorId', this.currentColorId);
    },
    reset() {
      this.$emit('update:priceFrom', 0);
      this.$emit('update:priceTo', 0);
      this.$emit('update:categoryId', 0);
      this.$emit('update:colorId', 0);
    },
    loadCategories() {
      axios
        .get(`${API_BASE_URL}/api/productCategories`)
        .then((response) => { this.categoriesData = response.data; });
    },
    loadColors() {
      axios
        .get(`${API_BASE_URL}/api/colors`)
        .then((response) => { this.colorsData = response.data; });
    },
  },
  created() {
    this.loadCategories();
    this.loadColors();
  },
};
</script>

<style>
  @import '../assets/css/style.min.css';
</style>
